
#maptools-snaptools input[type='checkbox'] {
  width: 29px;
  height: 29px;
  overflow: hidden;
  display: block;
  padding: 0;
  outline: none;
  border: 0;
  box-sizing: border-box;
  background-color: transparent;
  cursor: pointer;
  margin: 0;
  border-bottom: 1px solid #ccc;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

#maptools-snaptools input[type='checkbox']:last-of-type {
  border-bottom: none;
}

#maptools-snaptools input[type='checkbox']:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

#maptools-snaptools input[type='checkbox']:checked {
  background-color: rgba(0, 0, 0, 0.15);
}

#maptools-snaptools input[type='checkbox']:after {
  content: '';
  background-repeat: no-repeat;
  background-position: center;
  background-size: 18px;
  border-radius: 4px;
  width: 100%;
  height: 100%;
  display: inline-block;
}

