/* @import "~antd/dist/antd.css"; */

#root {
    width: 100%;
    height: 100%;
}

/* Header Menu */
.header-menu-button-left {
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
}

.header-menu-button-right {
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}

#geocoder {
    display: none;
}

/* header-container */
#logo-container {
    display: flex;
    justify-content: center; 
    align-items: center;
}

#logo {
    padding: 5px;
    width : 40px; 
    height : 40px;
}

#Text1_ {
    padding: 0px 0px 0px 5px;
    margin:0px;
    margin-top: 10px; 
    max-width: 65px;
}

#Text1_ span {
    font-family: Impact;
    font-size: 30px; 
    color: #fff;
    text-shadow: 0px 0px 3px #777;
}

#Text2_ {
    pointer-events: auto;
    display: flex;
    justify-content: left; 
    align-items: center; 
    padding : 11px 0px 0px 5px;
}

#Text2_ div {
    width:18px; 
    height:18px; 
    border-radius: 50%; 
    padding: 0px 5px; 
    font-family: mitr;
    box-shadow: 0px 0px 3px #777;
}

#Text2_ div div {
    margin: 0px 0px 0px -5px;
}