/* autocomplete */

.ui-autocomplete {
  max-height: 100px;
  overflow-y: auto;
  /* prevent horizontal scrollbar */
  overflow-x: hidden;
}
/* IE 6 doesn't support max-height
 * we use height instead, but this forces the menu to always be this tall
 */
* html .ui-autocomplete {
  height: 100px;
}

.ui-menu-item {
  font-family: "Serithai";
  font-size:12px;
  background-color: #eee;
}

/* custom scrollbar autocomplete dropdown */
/* width */

.ui-autocomplete::-webkit-scrollbar {
  width: 5px;
  height: 0px;
}

/* Track */
.ui-autocomplete::-webkit-scrollbar-track {
  background: #fff0; 
  padding-right: 2px;
}
 
/* Handle */
.ui-autocomplete::-webkit-scrollbar-thumb {
  background: #ccc; 
  border-radius: 2px;
}

/* Handle on hover */
.ui-autocomplete::-webkit-scrollbar-thumb:hover {
  background: #aaa; 
}


/* detail table  ********************************/

    .detailTable>tbody {
      border-top: 10px solid white;
      padding-top: 5px;
    }

    .detailTable>tbody>tr>td {
      padding: 0 5px;
      border-radius: 5px;
      /*background-color: #7777770f;*/
      border: 2px solid #fff;
    }

    .detailTable-container {
      width: 100%;
      height: calc(100% - 63px);
      padding: 5px;
      background-color: #7777770f;
      border-bottom-right-radius: 5px;
      border-bottom-left-radius: 5px;
    }
    .datailTable-scroll-container {
      width: 100%;
      height: 100%;
      padding: 0px;
      border-radius: 0px;
      overflow: auto;
    }

    .detailTable {
      width: 100%;
      padding: 5px 5px 10px 5px;
      border-collapse: separate;
      border-spacing: 0px;
      font-family: Serithai;
      font-size: 12px;
      background-color: #fff;
      color: #2a58c3;
      border-radius: 10px;
      cursor: normal;
    }
    .parent ~ .cchild {
      display: none;
    }
    .open .parent ~ .cchild {
      display: table-row;
    }
    .parent {
      cursor: pointer;
    }

    .open>.parent>.detailHeader {
      font-weight: bold;
/*      background-color: #2a58c3;
      color: #fff;*/
    }

    .parent > *:last-child {
      width: 30px;
    }
    .parent i {
      transform: rotate(0deg);
      transition: transform .3s cubic-bezier(.4,0,.2,1);
      margin: -.5rem;
      padding: .5rem;
     
    }
    .open .parent i {
      transform: rotate(180deg)
    }

    .header {
      font-weight: bold;
      cursor: pointer;
    }


    .detailTable>tbody>tr>.dstep {
      padding-left: 0px;      
      background-color: #fff;
    }

    .detailTable>tbody>tr>.dstep_date {
      float:right;
      background-color: #fff;
      margin-right:20px;
    }

    .detailTable>tbody>tr>.detailHeader {
      margin-top:3px;
      padding: 3px 5px 0 5px;
      background-color: #fff;
/*      color: #000;*/
      border: 1px solid #2a58c333;
      border-radius: 5px;
    }

    .detailHeader_col2 {
      float: right;
    }

    .icon_dropdown {
      margin-left:10px;
    }

    .cchild {
      vertical-align: text-top;
    }

    .topic {
      border-radius: 0;
      background-color: #3332;
      /*color: #fff; */     
      border: 0px  solid #fff;
      border-top: 3px solid #fff;
      border-bottom: 1px solid #fff;
      font-weight: bold;
    }

    .detailTable>tbody>tr>.bgblank, .detailTable>tbody>tr>.icon-circle, .detailTable>tbody>.topic>.bgblank {
      background-color: #fff;
    }

    .detailTable>tbody>tr>.icon-circle {
      width: 20px;
    }

