input.slide-toggle {
  margin: 3px 15px 3px 0px;
  /* margin-right: 15px;
  margin-left: 0px; */
  cursor: pointer;
  border: solid thin #ccc;
  position: relative;
  -webkit-appearance: none;
  outline: none;
  top: 2px;
  width: 22px;
  height: 7px;
  background-color: #eee;
  border-radius: 22px;
  transition: 0.2s;
  background-clip: padding-box;
}

input.slide-toggle:after {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  background: #fff;
  width: 10px;
  height:10px;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px #ccc;
  transition: 0.2s;
}

input.slide-toggle:focus {
  border-color: #0d84b3; 
  box-shadow: 0 0 0 0.1rem rgba(13,132,179,.2);
}

input.slide-toggle:checked {
  background: #0d84b3;
  border-color: #0d84b3;
}

input.slide-toggle:checked:after {
  left: 12px;
  box-shadow: 0 0 1px 2px #0d84b3;
}
/**
SPECTRE MODAL
**/
.modal {
  align-items: center;
  bottom: 0;
  display: none;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  justify-content: center;
  left: 0;
  opacity: 0;
  overflow: hidden;
  /*padding: .4rem;*/
  position: fixed;
  right: 0;
  top: 0;
}

.modal:target,
.modal.active {
  display: flex;
  display: -ms-flexbox;
  opacity: 1;
  z-index: 400;
}

.modal:target .modal-overlay,
.modal.active .modal-overlay {
  background: rgba(248, 249, 250, .75);
  bottom: 0;
  cursor: default;
  display: block;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.modal:target .modal-container,
.modal.active .modal-container {
  animation: slide-down .2s ease 1;
  z-index: 1;
}

.modal.modal-sm .modal-container {
  max-width: 320px;
  padding: 0 .4rem;
}

.modal.modal-lg .modal-overlay {
  background: #fff;
}

.modal.modal-lg .modal-container {
  box-shadow: none;
  max-width: 960px;
}

.modal-container {
  background: #fff;
  border-radius: .1rem;
  box-shadow: 0 .2rem .5rem rgba(69, 77, 93, .3);
  display: flex;
  display: -ms-flexbox;
  -ms-flex-direction: column;
  flex-direction: column;
  max-height: 75vh;
  max-width: 640px;
  padding: 0 .8rem;
  width: 100%;
  /*font-family: 'Serithai';*/
  color:#454d5d;

  border-radius: 5px;
}

.modal-container.modal-fullheight {
  max-height: 100vh;
}

.modal-container .modal-header {
  color: #000; /*#454d5d;*/
  padding: .8rem;
  padding-bottom: 0px;
}

.modal-container .modal-body {
  overflow-y: auto;
  padding: .8rem;
  position: relative;
}

.modal-container .modal-footer {
  /*padding: .8rem;*/
  padding: 0px;
  text-align: right;
}


