.mapboxgl-style-list
{
    display: none;
}

.mapboxgl-ctrl-group .mapboxgl-style-list button
{
    background: none;
    border: none;
    cursor: pointer;
    display: block;
    font-size: 14px;
    font-weight: 500;
    padding: 4px 8px 4px 8px;
    text-align: right;
    width: 100%;
    height: auto;
}

.mapboxgl-ctrl-group .mapboxgl-style-list button:after {
    content: " ";
}

.mapboxgl-style-list button.active
{
    font-weight: 900;
}

.mapboxgl-style-list button.active:after {
    content: " ";
}

.mapboxgl-style-list button:hover
{
    background-color: rgba(0, 0, 0, 0.05);
}

.mapboxgl-style-list button + button
{
    border-top: 1px solid #ddd;
}

.mapboxgl-style-switcher
{ 
    /* background-image: url(data:image/svg+xml;base64,); */
    background-image: url('./img/style.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 80%;
}
